import $axios from "axios";
import { generateUUID } from "./tools";
const axios = $axios.create();
export async function ossUpload(file, ossOpts, cb = () => {}) {
  const request = new FormData();
  const uuid = generateUUID();
  const src = `${ossOpts.dir}${uuid}`;
  request.append("OSSAccessKeyId", ossOpts.app_id); // Bucket Access Key Id
  request.append("name", uuid);
  request.append("policy", ossOpts.policy); // policy
  request.append("Signature", ossOpts.signature); // Access Key SecretpolicyOSSPost
  request.append("key", src); // 
  request.append("region", ossOpts.region);
  request.append("dir", ossOpts.dir);
  request.append("success_action_status", "200"); // 200,204
  request.append("file", file, uuid);
  const result = `${ossOpts.showurl}/${src}`;
  try {
    await axios.post(ossOpts.showurl, request, {
      onUploadProgress: cb
    });
    return {
      uuid,
      url: result,
      err: null
    };
  } catch (err) {
    console.log("OSS", err);
    return {
      err
    };
  }
}
